:root{

    --simpleTaxPrimary: rgb(65,202,208);
}


.form-vertical
{
    position: relative;
    
}
.tbody
{
    font-size: 0px;
    margin-left: 20px;
    
}
@font-face{
    font-family: 'Helvetica 65 Medium';
    font-style:normal;
    font-weight: normal;
    src: local('Helvetica 65 Medium'), url("fonts/HelveticaNeue-Medium.woff") format("woff"), url("fonts/HelveticaNeue-Medium.otf") format("opentype");
}
@font-face{
    font-family: 'Helvetica Neue Light';
    font-style:normal;
    font-weight: normal;
    src: local('Helvetica Neue Light'), url("fonts/HelveticaNeueLight.woff")format("woff"), url("fonts/HelveticaNeueLight.ttf") format("truetype");
}
#Title
{
