body {
    font-family: 'Poppins', sans-serif !important;
    background: #fafafa;
    margin: auto;
 

  }
  .wrapper{
      font-size: 10px;
      
      
  }



:root{

    --simpleTaxPrimary: rgb(65,202,208);
}


.form-vertical
{
    position: relative;
    
}
.tbody
{
    font-size: 0px;
    margin-left: 20px;
    
}
@font-face{
    font-family: 'Helvetica 65 Medium';
    font-style:normal;
    font-weight: normal;
    src: local('Helvetica 65 Medium'), url(/static/media/HelveticaNeue-Medium.344cf33e.woff) format("woff"), url(/static/media/HelveticaNeue-Medium.6c6db39a.otf) format("opentype");
}
@font-face{
    font-family: 'Helvetica Neue Light';
    font-style:normal;
    font-weight: normal;
    src: local('Helvetica Neue Light'), url(/static/media/HelveticaNeueLight.ece754bf.woff)format("woff"), url(/static/media/HelveticaNeueLight.9ce6b81d.ttf) format("truetype");
}
#Title
{
    font-size: 35px;
    font-weight: 800;
    color: var(--primaryB1);
    font-family: 'Helvetica 65 Medium';
    margin-left: 10px;
    margin-top: 10px;

}

#SimpleTaxText{
    font-size:15px;
    font-weight: 400;
    color: gray;
    font-family: 'Helvetica 65 Medium';
    margin-left:60px;
    margin-top: 30px;
}

#regularInputRow{
    margin-left: 50px;
    height:40px;
  
    margin: 10px;

}

#inputField
{
    background: beige;
    font-size: 12px;
    height: 30px;
    width:200px;
    color: black;
    font-weight: 600;
    text-align: right;
    margin-left: 10px;
    margin-right: 10px;
}




